body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.art {
  position: relative;
  text-align: center; }
  .art:active {
    box-shadow: inset 0 0 10px gainsboro; }

.track {
  border: solid thin gainsboro;
  position: relative;
  text-align: left;
  margin: 5px;
  padding: 4px;
  box-shadow: 1px 1px 1px gainsboro;
  font-size: 15px;
  cursor: pointer;
  background-color: white; }
  .track:hover {
    background-color: beige; }
  .track:active {
    box-shadow: inset 0 0 10px gainsboro; }

.fly {
  -webkit-animation: fly 1s linear;
          animation: fly 1s linear; }

@-webkit-keyframes fly {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    position: absolute;
    top: 30%;
    width: 290px;
    left: calc(10%);
    height: 40px; }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    top: 50px;
    position: absolute;
    width: 400px;
    left: calc(90%/3 + 100px);
    height: 40px; } }

@keyframes fly {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    position: absolute;
    top: 30%;
    width: 290px;
    left: calc(10%);
    height: 40px; }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    top: 50px;
    position: absolute;
    width: 400px;
    left: calc(90%/3 + 100px);
    height: 40px; } }

@media only screen and (max-width: 617px) {
  .fly {
    -webkit-animation: fly 1s linear;
            animation: fly 1s linear; }
  @-webkit-keyframes fly {
    0% {
      position: absolute;
      -webkit-transform: scale(0);
              transform: scale(0);
      top: 0; }
    100% {
      position: absolute;
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
      top: 100%;
      width: 400px;
      height: 40px; } }
  @keyframes fly {
    0% {
      position: absolute;
      -webkit-transform: scale(0);
              transform: scale(0);
      top: 0; }
    100% {
      position: absolute;
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
      top: 100%;
      width: 400px;
      height: 40px; } } }

.btn {
  font-size: 17px; }
  .btn .next-button {
    float: left;
    margin: 2px; }
  .btn .view-buttons {
    float: right;
    margin: 2px; }

.input-wrapper {
  margin: 2px;
  display: -webkit-flex;
  display: flex;
  width: 100%; }
  .input-wrapper .input {
    width: 100%; }

.search {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column; }
  .search .search-results {
    -webkit-flex-basis: 86%;
            flex-basis: 86%;
    overflow-y: scroll; }
  .search .next-button {
    float: left;
    margin: 2px; }
  .search .view-buttons {
    float: right;
    margin: 2px; }

.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  margin: auto; }
  .App .container {
    min-width: 300px;
    box-shadow: 2px 2px 1px gainsboro;
    border: solid thin gainsboro;
    -webkit-flex-basis: 32%;
            flex-basis: 32%;
    height: 500px;
    margin: 3px; }

